<template>
    <v-content>
        <h2>New Blocks</h2>
        <v-container class="grey lighten-5">
            <v-row>
                <v-col v-for="block in blocks" :key="block.blockNumber" cols="12" sm="3">
                    <v-slide-x-transition>
                    <v-card>
                        <v-card-text>
                            <div>New Block</div>
                            <p># {{block.number}}</p>
                            <div class="text--primary">
                                Hash: {{block.hash}}<br>
                                Miner: {{block.miner}}
                            </div>
                        </v-card-text>
                    </v-card>
                    </v-slide-x-transition>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
</template>

<script>
  export default {
    name: 'NewBlocksComponent',
    data: () => ({
      blocks: []
    }),

    sockets: {
      serverChannel(data) {
        this.blocks.push(data);
        if (this.blocks.length > 8)
          this.blocks = []
        // from socket io we have to forceUpdate on vue otherwise the ui is not refreshed
        this.$forceUpdate();
      }
    },
  };
</script>
